    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 1.5%;
  }
}
@media (max-width: 1100px) {
  .main-card-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 769px) {
  .main-card-list {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 15px;
  }
}
