.sidebar {
  width: 130px;
  height: 100vh;
  background-color: #313030;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.logo {
  pointer-events: none;
  margin: 25px 0;
}
.sidebar-list {
  width: 90%;
  height: 100%;
  align-self: flex-end;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


.sidebar-list-items {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;  
}
.sidebar-list-item {
  margin: 35px 0;
}
.sidebar-list-wrapper {
  height: 95px;
  background-color: #F2F2F2;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  position: relative;
  right: -1px;
  top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.after, .before {
  position: absolute;
  width: 20%;
  height: 21%;
  right: 0;
  background-color: #F2F2F2;
}
.after {
  top: -20%;
}
.before {
  top: 99%;  
}
.after::after, .before::before {
  position: absolute;
  content: "";
  background-color: #313030;
  width: 100%;
  height: 100%;
  left: -1px;
}
.after::after {
  border-bottom-right-radius: 15px;
  top: -1px;
}
.before::before {
  border-top-right-radius: 15px;
  top: 1px;
}
.sidebar-list__btn {
  width: 70%;
  height: 70%;
  background: linear-gradient(to bottom right, #FEC163, #DE4313);
  border-radius: 15px;
}

